.container-form {
    height: auto;
}

.my-container,
.fields,
.fields fieldset,
.user-fields {
    width: 100%;
}

.fields,
.form {
    width: 100%;
    display: flex;
}

.form {
    height: auto;
}

.fields,
.form {
    flex-direction: column;
}

.fields a {
    cursor: pointer;
}

fieldset {
    border: none;
}

.form {
    padding-left: 0px;
    padding-right: 0px;
}

.form input {
    border-radius: 0px;
    padding-left: 15px;
    font-size: 13px;
    font-weight: 300;
    color: #fff;
    background-color: rgba(250, 250, 250, 0.15);
    outline: none;
    border: none;
    box-shadow: none;
    line-height: 40px;
    height: 40px;
    width: 100%;
    margin-bottom: 25px;
}

.form textarea {
    border-radius: 0px;
    padding-left: 15px;
    padding-top: 10px;
    font-size: 13px;
    font-weight: 300;
    color: #fff;
    background-color: rgba(250, 250, 250, 0.15);
    outline: none;
    border: none;
    box-shadow: none;
    height: 170px;
    max-height: 220px;
    width: 100%;
    max-width: 100%;
    margin-bottom: 25px;
    resize: none;
}

.navigation-btn {
    text-align: center;
    max-width: 100%;
    width: 100%;
    display: inline-block;
    font-size: 1.5rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #fff;
    text-decoration: none;
    background-color: transparent;
    transition: all 0.5s;
}

.navigation-btn:hover {
    color: rgba(136, 165, 191, 0.48);
}

.navigation-btn:hover::after {
    display: block;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 1px;
    width: 50%;
    margin: 10px auto;
    background: rgba(136, 165, 191, 0.48);
    content: '';
}

.my-container,
.container-contact {
    margin: 0 auto;
}

.container-contact {
    width: 90%;
    height: 90%;
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
}

.container-contact ul {
    padding: 0;
    list-style-type: none;
}

.container-contact h1 {
    width: 100%;
    text-align: center;
    margin: 0 0 40px 0;
}

.head{
    display: flex;
}

.head fieldset{
    width: 50%;
}

.head ul li {
    padding: 5px;
    display: flex;
    justify-content: flex-start;
}

.head ul li i {
    font-size: 1.1rem;
    text-align: left;
    margin-top: 10px;
    width: 30px;
}

.head ul li span {
    font-size: 1.3rem;
    width: 100%;
}

.head fieldset,
.map fieldset {
    margin: 20px 0;
}

.map fieldset:first-child {
    margin-top: 40px;
}

.map fieldset:last-child {
    margin-bottom: 40px;
}

.form,
.view-map{
    margin: 40px 0;
}

@media screen and (min-width: 1024px) {
    .fields {
        flex-direction: row;
    }

    .container-contact {
        width: 98%;
    }

    .user-fields {
        margin-right: 10px;
    }
}